.filter {
  padding: 1rem;
  border-radius: 25px;
  background-color: #ccf2f4;
  text-align: center;
  height: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 2px 2px 5px #aaaa;
}
.yearSelector {
  text-align: right;
}
.yearSelector select {
  border-radius: 25px;
  padding: 0.3rem;
  border: none;
}
