.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 20px;
  border-radius: 12px;
  background-color: whitesmoke;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #6096b4;
  width: 100%;
  transition: all 0.5s ease-out;
}

.chart-bar__label {
  margin-top: 10px;
  font-weight: bold;
  font-size: 0.8rem;
  text-align: center;
}
