.header {
  box-shadow: 2px 2px 5px #aaaa;
  border-radius: 25px;
  background-color: #ccf2f4;
  position: relative;
  margin-bottom: 20px;
  margin-top: 20px;
}

.button--create-expense {
  cursor: pointer;
  font-weight: bold;
  position: absolute;
  display: inline-block;
  padding: 1rem;
  border-radius: 25px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: whitesmoke;
  border: none;
  position: relative;
  background-color: rgba(0, 255, 0, 0.4);
}
.button--create-expense:hover {
  background-color: rgba(0, 255, 0, 0.8);
  padding: 1.3rem;
}
