.expense__form {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1rem;
}

.expense__form input {
  border-radius: 25px;
  border: none;
  padding: 0.3rem;
  background-color: whitesmoke;
}
.line--form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.line--form label {
  margin-right: 5rem;
}
.btn__form button {
  margin-top: 0.5rem;
  border-radius: 25px;
  padding: 1rem;
  margin-right: 2rem;
  margin-left: 2rem;
  border: none;
  background-color: whitesmoke;
}

.btn__form .cancel__btn {
  background-color: rgba(255, 38, 0, 0.4);
}
.btn__form .send__btn {
  background-color: rgba(0, 255, 0, 0.4);
}

.btn__form .send__btn:hover {
  font-weight: bolder;
  padding: 1.15rem;
  background-color: rgba(0, 255, 0, 0.8);
}
.btn__form .cancel__btn:hover {
  font-weight: bolder;
  padding: 1.15rem;
  background-color: rgba(255, 38, 0, 0.8);
}
